@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: 'Inconsolata', monospace;
   box-sizing: border-box;
}
html{
  font-size: 16px;
}

.App{
  height: 100vh;
}

.notes{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.notfound{
  text-align: center;
  margin: 2rem auto;
}
.header{
    width: 100%;
    height: 100px;
    background-color:#f1c40f;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

}
.header h1{
    font-size: 2rem;
    font-weight: 500;
    color: #fff;
}

.createnote{
    max-width: 70%;
    padding: 2rem;
    margin: 2rem auto;
    box-shadow: 0 1rem 1rem rgb(148, 147, 145);
    border-radius: 1rem;
    border: 2px solid red;
    position: relative;
}
.createnote input, .createnote textarea{
    width: 100%;
    font-size: 1.3rem;
    font-weight: 400;
    color: grey;
    outline: none;
    border: 0;
    margin-bottom: 1rem;
}
.createnote input{
    font-weight: bold;
}
.createnote button{
   border: 0;
   border-radius: 30px;
   box-shadow: 0 0.5rem 1rem grey;
   cursor: pointer;
   bottom: -1rem;
   position:absolute;
   right:2rem;
   width: 3rem;
   height: 3rem;
   color: yellow;
   background-color: white;


}
.createnote button:active{
    opacity: .6;
}

.singlenote{
    margin: 2rem;
    min-width: 300px;
    height: 150px;
    box-shadow: 0 1rem 1rem rgb(148, 147, 145);
    border-radius: 1rem;
    border: 2px solid green;
    position: relative;
    padding: 1rem;
}
.singlenote p{
    margin-top: 1rem;

}

.singlenote button{
    border: 0;
   border-radius: 30px;
   box-shadow: 0 0.5rem 1rem grey;
   cursor: pointer;
   bottom: .1rem;
   position:absolute;
   right:1rem;
   width: 2rem;
   height: 2rem;
   color: black;
   background-color: white;

}
.singlenote button:active{
    color:red;
}
